import { FC, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Button, Table } from "antd";
import type { ColumnsType } from "antd/es/table";
import { deleteEmail, getAllEmails } from "../api/admin";
import AddEmail from "./AddEmail";

interface DataType {
  key: string;
  email: string;
}

const AllEmails: FC = () => {
  const { secret } = useParams();
  const [emailsDataSource, setEmailsDataSource] = useState<DataType[]>([]);

  const onGetAllEmails = async () => {
    if (!secret) return;

    const emails = await getAllEmails(secret);

    if (!emails.length) return;

    const emailsDataSource: DataType[] = emails.map((email) => ({
      key: email,
      email,
    }));

    setEmailsDataSource(emailsDataSource);
  };

  const onDeleteEmail = async (email: string) => {
    if (!secret) return;

    const isSuccess = await deleteEmail(secret, email);

    isSuccess && onGetAllEmails();
  };

  const columns: ColumnsType<DataType> = [
    {
      title: "Email",
      dataIndex: "email",
      key: "email",
    },
    {
      title: "Action",
      key: "action",
      render: (_, record) => (
        <Button
          onClick={() => onDeleteEmail(record.email)}
          type="primary"
          danger
        >
          Delete
        </Button>
      ),
    },
  ];

  useEffect(() => {
    onGetAllEmails();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <Table columns={columns} dataSource={emailsDataSource} />
      <AddEmail onGetAllEmails={onGetAllEmails} />
    </>
  );
};

export default AllEmails;
