import { api } from ".";

export const getAllEmails = async (secret: string): Promise<string[]> => {
  const res = await api.get(`/emails/${secret}`);

  return res.data || [];
};

export const addEmail = async (
  secret: string,
  email: string
): Promise<boolean> => {
  const res = await api.post(`/emails/${secret}`, { email });

  return res.status === 200;
};

export const deleteEmail = async (
  secret: string,
  email: string
): Promise<boolean> => {
  const res = await api.delete(`/emails/${secret}/${email}`);

  return res.status === 200;
};
