import { FC, useState } from "react";
import { FloatButton, Input, Modal } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { addEmail } from "../api/admin";
import { useParams } from "react-router-dom";

interface IAddEmailProps {
  onGetAllEmails: Function;
}

const AddEmail: FC<IAddEmailProps> = ({ onGetAllEmails }) => {
  const { secret } = useParams();

  const [isAddEmailModalOpen, setIsAddEmailModalOpen] = useState(false);
  const [newEmailValue, setNewEmailValue] = useState("");

  const onAddEmail = async () => {
    if (newEmailValue.trim().length > 0 && secret) {
      const isSuccess = await addEmail(secret, newEmailValue);

      if (isSuccess) {
        setIsAddEmailModalOpen(false);
        onGetAllEmails();
      }
    }
  };

  return (
    <>
      <Modal
        title="Add Email"
        open={isAddEmailModalOpen}
        okText="Add"
        onOk={onAddEmail}
        onCancel={() => {
          setIsAddEmailModalOpen(false);
        }}
      >
        <Input
          placeholder="someone@example.com"
          value={newEmailValue}
          onChange={(event) => {
            setNewEmailValue(event.target.value);
          }}
        />
      </Modal>
      <FloatButton
        onClick={() => {
          setIsAddEmailModalOpen(true);
        }}
        shape="square"
        type="primary"
        icon={<PlusOutlined />}
      />
    </>
  );
};

export default AddEmail;
